.browser-support {
  display: none;
  width: 100%;
  text-align: center;
  font-family: 'Gotham', Arial, sans-serif;
}
.browser-support img {
  display: block;
  margin: 4em auto;
}
.browser-support p {
  margin-bottom: 1em;
  color: #777;
}
.browser-ie .bottle-container {
  display: none;
}
.browser-ie .browser-support {
  display: block;
}
.browser-logos {
  margin-top: 2em;
  margin-bottom: 2em;
}
.browser-logos a {
  display: inline-block;
  width: 4em;
  margin: 0 0.75em;
  text-decoration: none;
  color: #777;
}
.browser-logos a:hover {
  opacity: 0.8;
  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=80)';
  filter: alpha(opacity=80);
}
.browser-logos img {
  display: inline-block;
  width: 100%;
  height: auto;
  margin: 0;
}
.browser-logos .text {
  display: inline-block;
  margin-top: 0.5em;
}
